// =====================================================================
// Epaper
// =====================================================================

$color-brand: #a50034;
$color-u-weekly: #00a5c0;
$color-red-shinmin: #ff1339;
$color-blue-wanbao: #0098de;
$color-grey: #464646;
$color-whitepale: #eaeaea;
$color-white: #ffffff;

.sec {
  .sec-epaper {
    background-color: #f9f9f9;
    display: flex;
    flex-wrap: nowrap;
    overflow-y: hidden;
    padding: 0 25px;

    &::-webkit-scrollbar,
    -ms-overflow-style,
    scrollbar-width {
      @media (min-width: 576px) {
        display: none;
      }
    }

    @media (min-width: 768px) {
      padding: 30px 0 0;
      display: flex;
      justify-content: center;
    }

    @media (min-width: 992px) {
      padding: 30px 0 67px;
      display: flex;
      justify-content: center;
    }
  }
}

.page-epaper {
  .epaper-content-deskstop {
    text-align: center;
    display: none;
    margin-bottom: 30px;

    @media (min-width: 576px) {
      display: block;
    }

    .link-underline {
      font-size: 22px;

      color: $color-brand;
      margin-bottom: 20px;
    }

    img {
      width: 100%;
    }
  }

  .epaper-content-mobile {
    display: block;
    margin: 0 auto 15px;
    padding: 0 15px;

    @media (min-width: 576px) {
      display: none;
    }

    img {
      width: 100%;
    }
  }

  .epaper {
    display: inline-block;
    text-align: center;
    min-height: 712px;
    min-width: 300px;
    margin-bottom: 30px;

    @media (min-width: 992px) {
      // @include aspect-ratio(3, 4);
      // width: calc(100% - 5px);
      width: 400px;
      height: auto;
    }

    &:first-child {
      margin-right: 15px;
      @media (min-width: 768px) {
        margin-right: 30px;
      }
    }

    &:last-child {
      margin-left: 15px;
      @media (min-width: 768px) {
        margin-left: 30px;
      }
    }

    .epaper-header {
      padding: 30px 48px 0;
      height: 300px;
      // height: 300px;
      // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);

      @media (min-width: 576px) {
        padding: 30px 10% 0;
        height: 350px;
      }

      @media (min-width: 768px) {
        padding: 30px 48px 0;
        height: 41%;
      }

      @media (min-width: 992px) {
        padding: 40px 65px 0;
        height: 37%;
      }

      @media (min-width: 1200px) {
        padding: 40px 65px 0;
        height: 406px;
      }

      &.epaper-zb {
        background-color: $color-brand;
      }

      &.epaper-sm {
        background-color: $color-red-shinmin;
      }

      &.epaper-wb {
        background-color: $color-blue-wanbao;
      }

      &.epaper-uweekly {
        background-color: $color-u-weekly;
      }
    }

    .epaper-content {
      height: auto;
      padding-top: 18%;
      background-color: #f9f9f9;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);

      @media (min-width: 576px) {
        padding-top: 20%;
      }

      @media (min-width: 992px) {
        padding-top: 33%;
      }

      @media (min-width: 1200px) {
        padding-top: 16%;
      }

      h3 {
        font-size: 18px;
        color: $color-grey;
        font-weight: 500;
      }

      ul {
        text-align: left;
        margin: 0 auto 24px;
        list-style-image: url('./../images/icon/red-tick.svg');
        padding: 0 46px 0 61px;

        @media (min-width: 768px) {
          margin: 0 auto 30px;
        }

        li {
          p {
            font-size: 14px;
            margin-bottom: 0;
          }
        }
      }

      .epaper-cta-container {
        margin: 0 17px;
        border-top: 1px solid $color-whitepale;
        position: relative;
        padding-bottom: 26px;
        padding-top: 60px;

        .epaper-flag {
          background-image: url(./../images/icon/promolabel.png);
          background-position: center center;
          background-size: contain;
          width: 159px;
          position: absolute;
          height: 60px;
          background-repeat: no-repeat;
          left: -32px;
          padding-top: 20px;
          top: -13px;

          h4 {
            font-size: 1.5rem;
            margin-left: -20px;
            letter-spacing: 2px;
          }

          &.subscriber {
            background-image: url(./../images/icon/promolabel_subscriber.png);
          }
        }

        .epaper-banner {
          padding-bottom: 30px;

          img {
            width: 100%;
          }
        }

        .epaper-cta {
          position: relative;
          // margin-top: 60px;

          h3 {
            padding-bottom: 20px;
            font-weight: normal;
            line-height: 140%;
          }

          .epaper-qr-code {
            margin: 0 auto;

            img {
              width: 50%;
              height: auto;

              @media (min-width: 768px) {
                width: 200px;
              }
            }
          }

          .epaper-unit {
            font-size: 14px;
          }

          .epaper-tag {
            font-size: 1rem;
            letter-spacing: 0.07px;
            padding: 2px 8px;
            line-height: normal;
            height: 20px;
            display: block;
            position: absolute;
            width: 60px;
            top: -14px;
            right: 0;
            z-index: 1;

            @media (min-width: 768px) {
              right: 16px;
            }
          }

          .cta {
            &.cta-solid {
              &.cta-solid-red {
                border-radius: 100px;
                padding: 5px 20px;
              }
            }
          }
        }
      }
    }

    &.epaper-subscribed {
      .epaper-header {
        height: auto;
        padding-top: 30px;
        // padding-bottom: 10px;

        p {
          margin-bottom: 0;
        }

        .epaper-img {
          padding-top: 20px;
        }
      }

      .epaper-content {
        padding-top: 20px;

        .epaper-cta-container {
          border: none;

          .epaper-cta {
            margin-top: 0;
          }
        }

        .epaper-carousel-container {
          .epaper-carousel {
            padding: 0 40px 26px;

            .slick-prev,
            .slick-next {
              top: 33%;
            }

            .slick-prev {
              left: 11px;
            }

            .slick-next {
              right: 11px;
            }

            &[dir='rtl'] {
              .slick-prev {
                left: 11px !important;
                right: auto !important;
              }
            }
          }

          .single-epaper {
            display: inline-block;
            width: 100px;
            padding: 0 6px;

            .epaper-image-holder {
              display: block;
              box-shadow: 4px 5px 5px -4px rgba(0, 0, 0, 0.42);
            }

            .epaper-date {
              &.zb {
                background-color: $color-brand;
              }

              &.shinmin {
                background-color: $color-red-shinmin;
              }

              &.uweekly {
                background-color: $color-u-weekly;
              }

              p {
                color: $color-white;
                font-size: 11px;
                font-weight: 500;
              }
            }

            img {
              width: 100%;
            }
          }
        }
      }
    }
  }

  .epaper-img {
    display: block;
    width: 100%;
  }
}
